import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'

const instance = Axios.create({
  baseURL: 'http://localhost:8000',
  timeout: 1000
})

Vue.use(Vuex)
Vue.prototype.$http = Axios


export const store = new Vuex.Store({


    state: {
      symptoms: []
     },


  mutations: {
     SET_SYMPTOMS: (state, symptoms) => state.symptoms = symptoms
   },

  actions: {
      getSymptoms: ({commit}) => {
         instance.get('/symptoms')
           .then(response => {
               commit('SET_SYMPTOMS', response.data)
           }).catch(err => console.log(err))
      }
    },


  getters: {
      getAllSymptoms: state => state.symptoms
    }
})



