import Vue from 'vue'
import Router from 'vue-router'
import {BootstrapVue, IconsPlugin} from 'bootstrap-vue'
import Home from "../pages/Home";
import Login from "../components/Login";

Vue.use(BootstrapVue, IconsPlugin)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    } ,
    {
       path: '/login',
      name: Login,
      component: Login
     }
  ]
})



