<template>

  <b-modal title="Login" id="login_modal" @cancel="handle = false" @close="handle = false">

    <form @submit="onHandle">

      <div class="form-group">
        <label for="username_input_id">Username</label>
        <input required type="text" name="" id="username_input_id" class="form-control" placeholder="Entrez votre username ici" aria-describedby="helpId">
        <small id="helpId" class="text-muted">Help text</small>
      </div>

       <div class="form-group">
        <label for="mdp_input_id">Mot De Passe</label>
        <input type="password" required name="" id="mdp_input_id" class="form-control" placeholder="Entrez votre username ici" aria-describedby="helpId">
        <small id="helpId2" class="text-muted">Help text</small>
      </div>

      <div class="form-group">
        <button type="submit" class="btn btn-danger">login</button>

        <b-spinner v-if="handle" small label="Loading..." style="margin-left: 15px"></b-spinner>

      </div>


    </form>

  </b-modal>


</template>

<script>
export default {
name: "Login",

data(){
  return {
    username: '',
    mdp: '',
    handle: false
  }
},

  methods:  {
    onHandle(event){
      event.preventDefault()
      this.handle = true

   }

}

}
</script>

<style scoped>

</style>



